//include in parent element, in case of float positioning (IE 8 and up)
@mixin clearfix() {
    &:after {
        content: "";
        display: block;
        clear: both;
    }
}

//для выранивания text-align: justify добавть в ::after родителю
@mixin justify() {
    content: "";
    display: inline-block;
    width: 100%;
}

@mixin products-grid($indent, $padding) {
    $indent_double: $indent*2;
    & {
        margin-left: (-$indent) - 1;
        margin-right: (-$indent) - 1;
        & > div:not(.product-horizontal-line) {
            margin-left: -$padding+$indent;
            margin-right: -$padding+$indent;
        }
        & > .product-horizontal-line {
            margin-left: -13px;
            width: calc(100% + 26px);
        }
    }
}

// indent = 12px, padding = 30px    indent_double = 24px;