.info-tips {
    padding: 0px 20px;
    &__list {
        // column-count: 2;
        @include clearfix;
    }
    &__item {
        margin-bottom: 60px;
        float: left;
        width: 50%;
        &:last-child {
            margin-bottom: 30px;
        }
    }
    &__title {
        color: map_get($blue, default);
        font-family: $merri-font;
        font-size: 26px;
        margin-left: 95px;
        margin-bottom: 30px;
    }
    &__text {
        font-size: 16px;
        font-weight: 400;
        display: inline-block;
        vertical-align: top;
        max-width: 465px;
        color:  map_get($gray, default);
    }
    /* &__text--light {
        color:  map_get($gray, default);
    }
    &__text--dark {
        color: #2b2b2b;
    } */
    &__icon {
        width: 65px;
        height: 58px;
        position: relative;
        background: #fff;
        border-radius: 15px;
        display: inline-block;
        vertical-align: top;
        margin-right: 30px;
    }
    &__icon-img {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }
}